import styled from 'styled-components';

export default styled.span`
  border-radius: 10px;
  font-size: 16px;
  margin: 5px;
  padding: 5px;
  background-color: darkgrey;
  cursor: pointer;
  color: black;
  &:hover {
    background-color: lightgrey;
  }
`;



